*{
    box-sizing: border-box;
    margin: 0%;
    padding: 0%;
}

#box{
    border: 1px solid black;
    width: 100%;
    height: 100vh;
    background-color: black;
}
#back{
    width: 40px;
    height: 40px;
    -webkit-filter: saturate(10000);
            filter: saturate(10000);
}

